<template>
    <div class="row">
        <div class="col-xs-24">
            <section-block :title="'Товар &quot;'+name+'&quot;'">
                <div id="gallery" class="acenter">
                    <a :href="url" v-for="url in images">
                        <img :src="url"/>
                    </a>
                </div>
                <div class="information">
                    <div class="good-title">{{name}}</div>
                    <div class="good-cat">{{categories[category]}}</div>
                </div>
                <div class="owner hidden-md hidden-lg hidden-xl hidden-xxl">
                    <router-link :to="'/chat/'+ owner.id">
                        <button class="btn btn-primary pull-right"><i class="fa fa-send"></i></button>
                    </router-link>
                    <router-link :to="'/user/'+ owner.id">
                        <img src="../../assets/images/avatar.png" alt="Avatar" class="img-circle">
                        <div class="fullname">{{owner.fullname}}</div>
                    </router-link>
                    <div class="rating"><el-rate
                            v-model="owner.rate"
                            disabled
                            text-color="#ff9900">
                    </el-rate></div>
                </div>
                <div class="information">
                    <div class="good-used">
                        <b>Состояние:</b>
                        <span v-if="used">Бывший в употреблении</span>
                        <span v-else>Новый</span>
                    </div>
                    <div class="good-limitations">
                        <b>Дефекты:</b>
                        <span v-if="limitations">{{flaw}}</span>
                        <span v-else>не найдены</span>
                    </div>
                    <div class="good-description">
                        <b>Описание:</b> {{description}}
                    </div>
                </div>
            </section-block>
        </div>
    </div>
</template>

<script>
    import sectionBlock from '../global/section.vue'
    import lg from 'lightgallery.js/dist/js/lightgallery.min'

    export default {
        name: 'goodPage',
        components: {
            sectionBlock,
        },
        data() {
            return {
                owner: {
                    id: 1,
                    fullname: 'Евгений Горбатов',
                    rate: 3.8,
                },
                categories: {
                    1: 'Аудио и видео',
                    2: 'Игры, приставки и программы',
                    3: 'Настольные компьютеры',
                    4: 'Оргтехника и расходники',
                    5: 'Планшеты и электронные книги',
                    6: 'Телефоны',
                    7: 'Товары для компьютера',
                    8: 'Фототехника',
                },
                types: {
                    buy: 'Продажа',
                    rent: 'Прокат',
                    selling: 'Реализация',
                    store: 'Хранение',
                    service: 'Сервис'
                },
                category: 1,
                name: 'Название товара',
                description: 'Описание',
                type: 'buy',
                price: 1000,
                used: false,
                limitations: true,
                flaw: '123',
                visible: false,
                images: [
                    'https://05.img.avito.st/640x480/3378714405.jpg',
                    'https://37.img.avito.st/640x480/3378714837.jpg',
                    'https://31.img.avito.st/640x480/3378715031.jpg',
                    'https://82.img.avito.st/640x480/3378715282.jpg',
                    'https://01.img.avito.st/640x480/3378715501.jpg',
                ]
            }
        },
        methods: {

        },
        mounted() {
            lightGallery(document.getElementById('gallery'));
        }
    }
</script>

<style lang="scss" src="@/assets/sass/light-gallery/lightgallery.scss"></style>

<style lang="sass" src="@/assets/sass/pages/goodInfo.sass"></style>


